// export default connect(null, actions)(App);

const App = () => {
    const dispatch = useDispatch();
    const auth = useSelector((state) => state.auth);

    useEffect(() => {
        dispatch(fetchUser());
    }, [dispatch]);

    return (
        <BrowserRouter>
            <Header />
            {!auth && <Route exact path="/" component={Landing} />}
            {auth && (
                <Route path="/surveys">
                    <p>Dashboard</p>
                </Route>
            )}
        </BrowserRouter>
    );
};

export default App;
